<template>
    <div  class="w-full h-full bg-[url(@/assets/hunting-bg.png)] bg-center bg-cover bg-no-repeat flex justify-center items-center flex-col gap-[2vh]">
        <div class="w-full h-[8vh]  flex justify-center items-center categorytitle">{{$store.state.translate.category}}</div>
        <div class="w-full h-[60vh]  flex justify-center items-center gap-[2vh]">
                <div class="w-[24%] h-[80%]  bg-[url(@/assets/bg-birds.png)] flex flex-col items-center bg-center bg-no-repeat bg-cover gap-[4vh] overflow-hidden typecontainer" style="border-radius: 12px;" v-bind:class="GetAccess('birds')">
                    <div class="w-full h-[20%]  flex justify-center items-center card-title"><p>{{$store.state.translate.birds}}</p></div>
                    <div class="w-[80%] h-[30%]  flex justify-center  card-desc" style="margin-top: 5vh;"><p>{{$store.state.translate.birdsdesc}}</p></div>
                    <div class="w-[60%] h-[12.5%]  flex justify-center items-center card-button flex justify-center items-center" @click="SetType('birdspage', 'birds', 'birds')">{{GetAccess('birds').text}}</div>
                </div>
                <div class="w-[24%] h-[80%]  bg-[url(@/assets/bg-animals.png)] flex flex-col items-center bg-center bg-no-repeat bg-cover gap-[4vh] overflow-hidden typecontainer" style="border-radius: 12px;" v-bind:class="GetAccess('animals')">
                    <div class="w-full h-[20%]  flex justify-center items-center card-title"><p>{{$store.state.translate.animals}}</p></div>
                    <div class="w-[80%] h-[30%]  flex justify-center  card-desc" style="margin-top: 5vh;"><p>{{$store.state.translate.animalsdesc}}</p></div>
                    <div class="w-[60%] h-[12.5%]  flex justify-center items-center card-button flex justify-center items-center" @click="SetType('animalspage', 'animals', 'animals')" style="background: #A49F6D;">{{GetAccess('animals').text}}</div>
                </div>
                <div class="w-[24%] h-[80%]  bg-[url(@/assets/bg-predators.png)] flex flex-col items-center bg-center bg-no-repeat bg-cover gap-[4vh] overflow-hidden typecontainer" style="border-radius: 12px;" v-bind:class="GetAccess('predators')">
                    <div class="w-full h-[20%]  flex justify-center items-center card-title"><p>{{$store.state.translate.predators}}</p></div>
                    <div class="w-[80%] h-[30%]  flex justify-center  card-desc" style="margin-top: 5vh;"><p>{{$store.state.translate.predatorsdesc}}</p></div>
                    <div class="w-[60%] h-[12.5%]  flex justify-center items-center card-button flex justify-center items-center" style="background: #A4776D;" @click="SetType('predatorspage', 'predators', 'predators')">{{GetAccess('predators').text}}</div>
                </div>
        </div>
        <div class="w-[20%] h-[7%] gobck  flex justify-center items-center" @click="GoBack">{{$store.state.translate.goback}}</div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'Disclaimer',
    props: {
      msg: String
    },
    methods : {
         GoBack() {
            this.$store.state.page = "main"
         },
         SetType(page, type, id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[id].requiredLevel) {
                this.$store.state.page = page
                this.$store.state.selecteddata.type = type
            }

         },
         GetAccess(id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[id].requiredLevel) {
                return {
                    'opacityup': true,
                    text : this.$store.state.translate.select
                }
            }
            return {
                text : 'Lv.'+this.$store.state.categories[id].requiredLevel+' '+this.$store.state.translate.required
            }
         }
      }
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    .categorytitle {
        font-family: 'Gilroy';
        font-size: 3vh;
        color: #FFFFFF;
    }
    .card-title{
        font-family: 'Gilroy';
        font-size: 2.5vh;
        line-height: 38px;
        text-align: center;
        color: #FFFFFF;
    }
    .card-desc {
        font-family: 'Gilroy-Semibold';
        font-size: 1.4vh;
        font-weight: 500;
        text-align: center;
        color: #FFFFFF;
        text-shadow: 0px 4px 17px rgba(0, 0, 0, 0.25);
    }
    .card-button{
        background: #98D6EA; 
        border-radius: 9px;
        font-family: 'Gilroy-Semibold';
        font-size: 1.6vh;
        text-align: center;
        color: #FFFFFF;
    }
    .gobck {
        background: #A49F6D;
        box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        font-family: 'Gilroy';
        font-size: 1.8vh;
        color: #000000;
    }

    .typecontainer {
        opacity: 65%;
    }
    .opacityup {
        opacity: 99%;
    }

  </style>
  