  <script>
  import $ from 'jquery';
  export default {
    name: 'Disclaimer',
    data() {
      return {
        clickedwikistate : 1,
    }
},
    props: {
      msg: String
    },
    
    methods : {
         SetClicked(id) {
              this.$store.state.checkbuttons[id] = !this.$store.state.checkbuttons[id]
         },
         GetText(isowner) {
            if (isowner) {
                return this.$store.state.translate.host
            }
            return this.$store.state.translate.hunter
         },
         GoBack() {
            this.$store.state.page = "hunt"
         },
         SelectTypeofHunt() {
            this.$store.state.page = "birdspage"
         },
         TurnMainMenu() {
            this.$store.state.page = "main"
         },
         CalculateTotalPrice(count, itemname) {
            if (this.$store.state.itemprices[itemname]) {
                count = Number(count)
                return count * this.$store.state.itemprices[itemname]
            }
         },
         SellItems() {
            //(this.$store.state.basket) 
                
            
         },

         HuntCoop() {
            var id = this.$store.state.lobbylist[this.$store.state.selectedRoom].id
            $.post('http://m-hunting/huntwithfellas', JSON.stringify({
				    id : id,
			    }));
            this.$store.state.page = "main"
            $(".main").fadeOut(300)
         },
         CheckPlayerIfOwner() {
            var playerlist = this.$store.state.lobbylist[this.$store.state.selectedRoom].playerlist
            var newData = {}
            playerlist.forEach(Element => {
                newData[Element.identifier] = Element.owner
            });
            //console.log(newData[this.$store.state.PlayerData.identifier])
            if (newData[this.$store.state.PlayerData.identifier] == true) {
                return true
            } else {
                return false
            }
         },
         LeaveLobby() {
            var playerlist = this.$store.state.lobbylist[this.$store.state.selectedRoom].playerlist
            var newData = {}
            playerlist.forEach(Element => {
                newData[Element.identifier] = Element.owner
            });
            if (newData[this.$store.state.PlayerData.identifier] == true) {
                // buraya luada oyuncularin hepsini lobiden silecek
                $.post('http://m-hunting/clearlobby', JSON.stringify({
				    id : this.$store.state.selectedRoom+1,
			    }));
                this.$store.state.page = 'coop'
                this.$store.state.playeranylobby.bool = false
            } else {
                // burayada oyuncu sadece kendi ayrilacak
                $.post('http://m-hunting/leavelobby', JSON.stringify({
				    id : this.$store.state.selectedRoom+1,
			    }));
                this.$store.state.page = 'coop'
                this.$store.state.playeranylobby.bool = false
            }
         },
         CheckLobbyType(type) {
            if (type == "private") {
                return this.$store.state.translate.private
            }
            return this.$store.state.translate.public
         },
         CheckVeh(getveh) {
            if (getveh) {
                return this.$store.state.translate.kamacho
            }
            return this.$store.state.translate.unknown
         },
         GetBackground(type) {
            if (type == "bird") {
                return {
                'bgbird': true,
                }
            } else if (type == "animal") {
                return {
                'bganimals': true,
                }
            } else if (type == "predator") {
                return {
                    'bgpredators': true,
                }
            } else {
                return {
                    'bgbird': true,
                }
            }

         },
         GetItemImage(itemname) {
            return require('../assets/'+itemname+'.png')
         },

         AddItemToBasket(itemdata) {
            if (this.$store.state.basket[itemdata.itemname]) {
                return
            }
            this.$store.state.basket[itemdata.itemname] = {
                count : itemdata.count,
                label : itemdata.label
            }
         },
         GetImage(image) {
            return require('@/assets/'+image+'')
         },
         SetPage(id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                this.$store.state.page = "gearbirds"
                this.$store.state.selecteddata.area = this.$store.state.categories[this.$store.state.selecteddata.type][id].name
            }
            
         },
         GetAccess(id) {
            //console.log(this.$store.state.selecteddata.type)
            //console.log(JSON.stringify(this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel))
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                return {
                    'opacityup': true,
                    text : this.$store.state.translate.select
                }
            }
            return {
                text : 'Lv.'+this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel+' '+this.$store.state.translate.required
            }
         }
      },

    
  }
  </script>
  
