<template>
    <div  class="w-full h-full bg-[url(@/assets/stats-bg.png)] bg-center bg-cover bg-no-repeat flex justify-center items-center flex-col gap-[2vh]">
        <div class="w-full h-[8vh]   flex justify-center   items-center  categorytitle">
            <p >{{$store.state.translate.market}}</p>
            <div class="w-[10%] h-[8%]   absolute flex justify-center items-center gotomainmenu gap-3" @click="TurnMainMenu" style="margin-right: 75vw;">
                <i class="fas fa-chevron-left" style="font-size: 1.3vw;"></i>
                {{$store.state.translate.mainmenu}}
            </div>
        </div>
        <div class="w-full h-[75vh]  flex justify-center items-center gap-[2vh]  flex-col ">
            <div class="w-[60%] h-[88%]  flex gap-3 justify-between ">
                <div class="w-[65%] h-full  leftcontainer flex flex-col items-center gap-[2vh]">
                    <p class="itemsyouhave w-[90%]  h-[2vh] mt-5">{{$store.state.translate.itemsyouhave}}</p>
                    <div class="w-[90%] h-[55vh]  flex gap-2 flex-wrap overflow-auto">
                        <div v-for="(item, index) in $store.state.PlayerData.itemlist" class="w-[24%] h-[28%]   flex " >
                            <div class="w-full h-full flex items-center flex-col gap-[0.2vh] justify-between itemlisttt  bg-center bg-cover bg-no-repeat overflow-hidden" style="border-radius: 4px;" :class="GetBackground(item.type)" @click="AddItemToBasket(item)">
                                <div class="w-[80%] h-[15%] mt-2  flex justify-between">
                                    <div class="w-[40%] h-full  flex"><p class="itemcount" >x{{item.count}}</p></div>
                                    <div class="w-[40%] h-full  flex justify-end"><p class="itemcount">${{$store.state.itemprices[item.itemname].price}}</p></div>
                                </div>
                                <div class="w-[80%] h-[40%] flex justify-center items-center">
                                    <img :src="GetItemImage(item.itemname)">
                                </div>
                                <div class="w-[80%] h-[20%]  flex flex-col gap-1 justify-center items-center">
                                    <div class="w-[30%] h-[0.3vh]" style="-radius: 5px; background:#D9D9D9"></div>
                                    <div class="w-full h-[3vh] mrktitemname flex justify-center items-center">{{item.label}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="w-[32%] h-full  flex flex-col gap-[1vh] justify-between">
                    <div class="w-full h-[75%]  rightcontainer flex flex-col gap-1 items-center">
                        <div class="w-[80%]  h-[4vh]">
                            <p class="itemsyouhave w-[90%]  h-[2vh] mt-5">{{$store.state.translate.card}}</p>
                        </div>
                        <div class="w-[80%]  h-[40vh]  overflow-auto ">
                            <div v-for="(item, index) in $store.state.basket" class="w-full h-[3vh]  flex ">
                                <div class="w-[60%] h-full  flex items-center basketitemname">
                                    {{item.label}}
                                </div>
                                <div class="w-[20%] h-full  flex justify-center items-center basketcount">
                                    x{{ item.count }}
                                </div>
                                <div class="w-[20%] h-full  flex justify-center items-center basketprice">
                                    ${{ CalculateTotalPrice(item.count, index) }}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="w-full h-[20%]  sell flex justify-center items-center" style="border-radius: 10px;background: #A49F6D;" @click="SellItems">{{$store.state.translate.sell}}</div>
                </div>
            </div>
        </div>
    </div>
  </template>
  
  <script>
  import $ from 'jquery';
  export default {
    name: 'Disclaimer',
    data() {
      return {
        clickedwikistate : 1,
    }
},
    props: {
      msg: String
    },
    
    methods : {
         SetClicked(id) {
              this.$store.state.checkbuttons[id] = !this.$store.state.checkbuttons[id]
         },
         GoBack() {
            this.$store.state.page = "hunt"
         },
         SelectTypeofHunt() {
            this.$store.state.page = "birdspage"
         },
         TurnMainMenu() {
            this.$store.state.page = "main"
         },
         CalculateTotalPrice(count, itemname) {
            if (this.$store.state.itemprices[itemname]) {
                count = Number(count)
                return count * this.$store.state.itemprices[itemname].price
            }
         },
         SellItems() {
            //(this.$store.state.basket) 
            $.post('http://m-hunting/sellitems', JSON.stringify({
				items : this.$store.state.basket,
			}));  
            $(".main").fadeOut(300)
            this.$store.state.basket = {}
         },
         GetBackground(type) {
            if (type == "bird") {
                return {
                'bgbird': true,
                }
            } else if (type == "animal") {
                return {
                'bganimals': true,
                }
            } else if (type == "predator") {
                return {
                    'bgpredators': true,
                }
            } else {
                return {
                    'bgbird': true,
                }
            }

         },

         GetItemImage(itemname) {
            return require('../assets/'+itemname+'.png')
         },

         AddItemToBasket(itemdata) {
            if (this.$store.state.basket[itemdata.itemname]) {
                return
            }
            this.$store.state.basket[itemdata.itemname] = {
                count : itemdata.count,
                label : itemdata.label
            }
         },
         GetImage(image) {
            return require('@/assets/'+image+'')
         },
         SetPage(id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                this.$store.state.page = "gearbirds"
                this.$store.state.selecteddata.area = this.$store.state.categories[this.$store.state.selecteddata.type][id].name
            }
            
         },
         GetAccess(id) {
            //console.log(this.$store.state.selecteddata.type)
            //console.log(JSON.stringify(this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel))
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                return {
                    'opacityup': true,
                    text : this.$store.state.translate.select
                }
            }
            return {
                text : 'Lv.'+this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel+' '+this.$store.state.translate.required
            }
         }
      },

    
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
