<template>
  <div class="main w-screen h-screen z-1" style="display: none;">
    <div class="w-full h-full absolute flex justify-center items-center"  :class="{'disableBlur' : $store.state.openpasswordpopup.bool}" v-if="$store.state.openpasswordpopup.bool">
        <div class="w-[30%] h-[20%]  flex flex-col justify-evenly items-center" style="border-radius: 15px;background: #A4776D;">
            <div class="w-[90%] h-[20%] flex justify-between items-center gap-2">
                <p class="w-[20%] h-full flex items-center lobbyname truncate">{{$store.state.openpasswordpopup.data.name}}</p>
                <div class="w-[65%] h-[0.2vh] " style="background: rgba(255, 255, 255, 0.24);"></div>
                <div class="w-[7%] h-[80%]  rounded-full flex justify-center items-center closeicon" @click="$store.state.openpasswordpopup.bool = false" style="background: rgba(0, 0, 0, 0.25); color:white; font-size:1.8vh;"><img src="./assets/closeicon.png"></div>
            </div>
            <div class="w-[90%] h-[60%] flex justify-center  gap-[2vh] flex-col">
              <div class="w-[40%] h-[10%] flex  items-center pswrod">{{$store.state.translate.password}}</div>
              <div class="w-full  h-[40%] flex  items-center pswrod gap-2 justify-between">
                <input type="text" class="w-[60%]  h-full bg-transparent outline-none  listsearchinput" style="padding:1vh" v-model="password" :placeholder="$store.state.translate.typepassword" >
                <div class="joinbtn w-[38%] h-full flex   justify-center items-center" @click="JoinLobby">{{$store.state.translate.join}}</div>
              </div>
          </div>
        </div>
    </div>
    <div class="w-full h-full z-1" :class="{'blurbg' : $store.state.openpasswordpopup.bool}">
      
      <div class="absolute w-full h-[8vh]  flex justify-evenly items-center">
        <div class="w-[10%] h-[80%]  flex justify-center items-center">
           <img alt="" :src="$store.state.serverimage" class="">
        </div>
        <div class="w-[60%] h-[80%]  flex justify-center items-center">
          <img v-if="$store.state.page != 'disclaimer' || $store.state.page != 'turnoverweapon' || $store.state.page != 'takebadge'" alt="" src="./assets/hunting-nav.png">
        </div>
        <div class="w-[10%] h-[80%]  flex gap-2 justify-end items-center">
          <div class="w-[65%] h-[80%]  flex flex-col justify-center items-end ">
                <div class="w-full h-[60%] truncate flex justify-end items-end  playrname">{{$store.state.PlayerData.name}}</div>
                <div class="w-full h-[60%] truncate flex justify-end items-end  userlvl">Lv.{{$store.state.PlayerData.level}}</div>
          </div>
            <div class="w-[40%] h-full  flex justify-center items-center flex-col">
                  <div class="w-[75%] h-[80%] rounded-full  overflow-hidden" style="border: 2px solid #85513E;">
                      <img alt="" class="w-full h-full" :src="$store.state.PlayerData.photo">
                  </div>
            </div>

        </div>
    </div>

        <Disclaimer v-if="$store.state.page == 'disclaimer'" />
        <TurnOverWeapon v-if="$store.state.page == 'turnoverweapon'" />
        <TakeBadge v-if="$store.state.page == 'takebadge'"/>
        <Main v-if="$store.state.page == 'main'"/>
        <Hunt v-if="$store.state.page == 'hunt'"></Hunt>
        <Birds v-if="$store.state.page == 'birdspage'"></Birds>
        <GearBirds v-if="$store.state.page == 'gearbirds'"></GearBirds>
        <VehiclesBirds v-if="$store.state.page == 'vehiclebirds'"></VehiclesBirds>
        <ContractsBirds v-if="$store.state.page == 'contractsbirds'" ></ContractsBirds>
