        <CoopLobbies v-if="$store.state.page == 'cooplobbies'"></CoopLobbies>
        <RoomInfo v-if="$store.state.page == 'roominfo'"></RoomInfo>
        <CreateLobby v-if="$store.state.page == 'createlobby'"></CreateLobby>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import Disclaimer from './components/Disclaimer.vue'
import TurnOverWeapon from './components/TurnOverWeapon.vue'
import TakeBadge from './components/TakeBadge.vue'
import Main from './components/Main.vue'
import Hunt from './components/Hunt.vue'
import Birds from './components/Birds.vue'
import GearBirds from './components/Gears-bird.vue'
import VehiclesBirds from './components/Vehicles-birds.vue'
import ContractsBirds from './components/Contracts-birds.vue'
import LetsHunt from './components/LetsHunt.vue'
import LetsHuntAnimals from './components/LetsHuntAnimals.vue'
import LetsHuntPredators from './components/LetsHuntPredators.vue'
import Animals from './components/Animals.vue'
import GearAnimals from './components/Gears-animals.vue'
import VehiclesAnimals from './components/Vehicles-animals.vue'
import Predators from './components/Predators.vue'
import GearPredators from './components/Gears-predators.vue'
import VehiclesPredators from './components/Vehicles-predators.vue'
import ContractsAnimals from './components/Contracts-animals.vue'
import ContractsPredators from './components/Contracts-predators.vue'
import Wiki from './components/Wiki.vue'
import LeaderBoard from './components/Leaderboard.vue'
import Stats from './components/Stats.vue'
import Market from './components/Market.vue'
import Coop from './components/Coop.vue'
import CoopLobbies from './components/CoopLobbies.vue'
import RoomInfo from './components/Roominfo.vue'
import CreateLobby from './components/CreateLobby.vue'
export default {
  data () {
    return {
      password : ''
    }
  },

  methods: {
    JoinLobby() {
      if (this.password == this.$store.state.openpasswordpopup.data.password) {
        // join lobby
        this.$store.state.openpasswordpopup.bool = false
        this.$store.state.selectedRoom = this.$store.state.openpasswordpopup.data.id
        this.$store.state.page = 'roominfo'
        this.$store.state.playeranylobby.bool = true
        this.$store.state.playeranylobby.id = this.$store.state.openpasswordpopup.data.id
        $.post('http://m-hunting/joinlobby', JSON.stringify({
				    id : this.$store.state.selectedRoom+1,
			}));
      }
    }
  },
  name: 'App',
  components: {
    Disclaimer,
    TurnOverWeapon,
    TakeBadge,
    Main,
    Hunt,
    Birds,
    GearBirds,
    VehiclesBirds,
    ContractsBirds,
    LetsHunt,
    Animals,
    GearAnimals,
    VehiclesAnimals,
    ContractsAnimals,
    LetsHuntAnimals,
    Predators,
    GearPredators,
    VehiclesPredators,
    ContractsPredators,
    LetsHuntPredators,
    Wiki,
    LeaderBoard,
    Stats,
    Market,
    Coop,
    CoopLobbies,
    RoomInfo,
    CreateLobby
    
  },
  mounted() {
    window.addEventListener("message", (event) => {
      if (event.data.action == "open") {
        // var disclaimer = localStorage.getItem("disclaimer");
        // var turnweapon = localStorage.getItem("turnoverweapon");
        // var takebadge = localStorage.getItem("takebadge");
        // if (!disclaimer) {
        //   this.$store.state.page = 'disclaimer'
        //   return
        // } else if (!turnweapon) {
        //   this.$store.state.page = 'turnoverweapon'
        //   return
        // } else if (!takebadge) {
        //   this.$store.state.page = 'takebadge'
        //   return
        // }
        this.$store.state.PlayerData = event.data.PlayerData
        this.$store.state.translate = event.data.translate
        this.$store.state.wiki = event.data.wiki
        this.$store.state.contracts = event.data.contracts
        this.$store.state.categories = event.data.categories
        this.$store.state.leaderboard = event.data.leaderboard
        this.$store.state.itemprices = event.data.itemprices
        this.$store.state.lobbylist = event.data.lobbylist
        this.$store.state.serverimage = event.data.serverimage
        this.$store.state.onlinehunters = event.data.TotalPlayers
        this.$store.state.desc = event.data.desc
        $(".main").fadeIn(300)
      } else if (event.data.action == "updateLobbyList") {
        this.$store.state.lobbylist = event.data.lobbylist
      } else if (event.data.action == "clearlobby") {
        if (this.$store.state.page == 'roominfo') {
          this.$store.state.page = 'cooplobbies'
        }
      } else if (event.data.action == "closenui") {
        $(".main").fadeOut(300)
      }
    })
    $(document).on('keydown', function() {
            switch(event.keyCode) {
            case 27: // ESC
                $(".main").fadeOut(300)
                $.post("https://m-hunting/close", JSON.stringify({}))
                break;
            }
        });
  },
}

//
</script>

