<template>
    <div  class="w-full h-full bg-[url(@/assets/stats-bg.png)] bg-center bg-cover bg-no-repeat flex justify-center items-center flex-col gap-[2vh]">
        <div class="w-full h-[8vh]   flex justify-center   items-center  categorytitle">
            <p >{{$store.state.translate.cooplobbies}}</p>
            <div class="w-[10%] h-[8%]   absolute flex justify-center items-center gotomainmenu gap-3" @click="TurnMainMenu" style="margin-right: 75vw;">
                <i class="fas fa-chevron-left" style="font-size: 1.3vw;"></i>
                {{$store.state.translate.mainmenu}}
            </div>
        </div>
        <div class="w-full h-[75vh]  flex justify-center items-center gap-[2vh]  flex-col ">
            <div class="w-[60%] h-[88%]  flex gap-3 justify-between  flex-col">
                <div class="w-full h-[5vh]  flex justify-between items-center">
                    <div class="w-[50%] h-[70%] flex justify-between items-center" style="border-radius: 10px; background: rgba(0, 0, 0, 0.32);">
                        <input type="text" class="w-[50%] h-[60%] bg-transparent outline-none  listsearchinput" style="padding:2vh" v-model="textsearch" :placeholder="$store.state.translate.searchlobby" name="search">
                        <i class="fas fa-search mr-5 searchicon"></i>
                    </div>
                    <p class="huntercount">{{$store.state.onlinehunters}} {{$store.state.translate.huntersonline}}</p>
                </div>
                <div class="w-full h-[58vh]  overflow-auto flex flex-wrap gap-[1vh] ">
                    <div v-for="(item, index) in GetFilteredResult()" class="w-[11.5vw] h-[24vh] flex-col  flex containerlist" v-bind:id="index">
                        <div class="w-full h-full  flex flex-col items-center mainn gap-[1vh] overflow-hidden opacity-60 hover:opacity-90"  :style="GetImage2($store.state.categories[item.hunttype.type][item.hunttype.selectedtype].image)" style="border-radius: 12px;" >
                            <div class="w-[90%] h-[20%] mt-2  flex justify-center items-center card-title"><p>{{item.name}}</p></div>
                            <div class="w-[80%] h-[30%]  flex justify-center items-center   card-desc" style="margin-top: 1vh;"><p>{{$store.state.categories[item.hunttype.type][item.hunttype.selectedtype].name}}</p></div>
                            <div class="w-[60%] h-[14.5%]  flex justify-center items-center card-button joinbutton flex justify-evenly items-center" :style="CalculateBackgroundColor(item.hunttype.type)" style="margin-top: 1.5vh;">
                                <div class="w-[30%] h-[80%]  flex justify-center items-center imageside ">
                                    <img alt="" :src="GetLockImage(item.requiredpassword)">
                                </div>
                                <div class="w-[60%] h-[80%]  flex justify-center items-center " @click="JoinLobby(item)">
                                    {{ $store.state.translate.join }}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="w-[10%] h-[5%] goback gap-3  flex justify-center items-center mt-5" @click="GoBack">
                <i class="fas fa-chevron-left" style="font-size: 1.3vw;"></i>
                {{$store.state.translate.goback}}
            </div>
        </div>
    </div>
    
  </template>
  
  <script>
    import $ from 'jquery';
  export default {
    name: 'Disclaimer',
    data() {
      return {
        clickedwikistate : 1,
        textsearch : ''
    }
},
    props: {
      msg: String
    },
    
    methods : {
         SetClicked(id) {
              this.$store.state.checkbuttons[id] = !this.$store.state.checkbuttons[id]
         },
         GoBack() {
            this.$store.state.page = "coop"
         },
         SelectTypeofHunt() {
            this.$store.state.page = "birdspage"
         },
         TurnMainMenu() {
            this.$store.state.page = "main"
         },
         CalculateTotalPrice(count, itemname) {
            if (this.$store.state.itemprices[itemname]) {
                count = Number(count)
                return count * this.$store.state.itemprices[itemname]
            }
         },
         SellItems() {
            //(this.$store.state.basket) 
                
            
         },

         JoinLobby(data) {
            if (data.requiredpassword) {
                this.$store.state.openpasswordpopup.data.name = data.name
                this.$store.state.openpasswordpopup.data.password = data.password
                this.$store.state.openpasswordpopup.data.id = data.id -1
                this.$store.state.openpasswordpopup.bool = true         
                return   
            }
            this.$store.state.selectedRoom = data.id-1
            this.$store.state.page = 'roominfo'
            this.$store.state.playeranylobby.bool = true
            this.$store.state.playeranylobby.id = data.id-1
            $.post('http://m-hunting/joinlobby', JSON.stringify({
				    id : this.$store.state.selectedRoom+1,
			}));
         },

         GetFilteredResult () {
                if (this.textsearch == '') {
                  return this.$store.state.lobbylist
                }
                return this.$store.state.lobbylist.filter(data => data.name.includes(this.textsearch))
        },
         CalculateBackgroundColor(type) {
            var color = "#98D6EA"
            if (type == "animals") {
                color = "#A49F6D"
            } else if (type == "predators" ) {
                color = "#A4776D"
            }
            return {
                background : color
            }
         },
         GetBackground(type) {
            if (type == "bird") {
                return {
                'bgbird': true,
                }
            } else if (type == "animal") {
                return {
                'bganimals': true,
                }
            } else if (type == "predator") {
                return {
                    'bgpredators': true,
                }
            } else {
                return {
                    'bgbird': true,
                }
            }

         },

         

         GetLockImage(reqpas) {
            if (reqpas) {
                return require('../assets/lock.png')
            }
            return require('../assets/unlock.png')
         },

         GetItemImage(itemname) {
            return require('../assets/'+itemname+'.png')
         },

         AddItemToBasket(itemdata) {
            if (this.$store.state.basket[itemdata.itemname]) {
                return
            }
            this.$store.state.basket[itemdata.itemname] = {
                count : itemdata.count,
                label : itemdata.label
            }
         },
         GetImage(image) {
            return require('@/assets/'+image+'')
         },

         GetImage2(image) {
            var imagte = require('@/assets/'+image+'')
            return {
                backgroundImage : 'url('+imagte+')'
            }
            
         },
         SetPage(id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                this.$store.state.page = "gearbirds"
                this.$store.state.selecteddata.area = this.$store.state.categories[this.$store.state.selecteddata.type][id].name
            }
            
         },
         GetAccess(id) {
            //console.log(this.$store.state.selecteddata.type)
            //console.log(JSON.stringify(this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel))
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                return {
                    'opacityup': true,
                    text : this.$store.state.translate.select
                }
            }
            return {
                text : 'Lv.'+this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel+' '+this.$store.state.translate.required
            }
         }
      },

    
  }
  </script>
  
