                                <div class="w-[80%] h-[30%]  flex justify-center items-center   card-desc" style="margin-top: 1vh;"><p>{{ item.description }}</p></div>
                                <div class="w-[60%] h-[14.5%]  flex justify-center items-center card-button joinbutton flex justify-evenly items-center" :style="CalculateBackgroundColor(item.type)" style="margin-top: 1.5vh;">
