                    <div class="w-[95%] h-[58vh]    flex gap-2 flex-wrap overflow-auto">
                        <div v-for="(item, index) in this.$store.state.contracts[this.$store.state.createlobbydata.hunttype.type]" class="w-[12.5vw] h-[23vh] flex-col items-center justify-center  bg-[url(@/assets/contract-bg.png)] bg-cover bg-no-repeat bg-center flex containerlist mainn opacity-70" @click="SelectContract(index)" :class="{'selected': $store.state.createlobbydata.contracts == index}" v-bind:id="index">
