                                {{item.title}}
                            </div>
                            <div class="w-[80%] h-[40%] flex justify-center items-center text-center  contractdesc">{{$store.state.translate.pigeonfdesc}}</div>
                            <div class="w-[80%] h-[20%] flex justify-center flex-col items-center   contractitle" style="font-size: 1.9vh;">
                                <p>${{item.money}}</p>
                                <p>{{item.xp}} {{$store.state.translate.xp}}</p>
                            </div>
                        </div>
                    </div>
                </div>



                <div class="w-[32%] h-full  flex flex-col gap-[1vh] justify-between">
