                                        {{ $store.state.translate.public }}
                                    </div>
                                    <div class="w-[50%] h-full flex justify-center items-center selector" @click="SetSelector('type', 'private')" :class="{'chose': $store.state.createlobbydata.type == 'private'}">
                                        {{ $store.state.translate.private }}
                                    </div>
                                </div>
                                
                            </div>

                            <div class="w-full h-[4vh]  mt-3 flex justify-between">
