                                <div class="w-[50%] h-full  flex items-end justify-end">
                                    <div class="w-[25%] h-full flex justify-center items-center selector" @click="SetSelector('size', 2)" :class="{'chose': $store.state.createlobbydata.size == 2}">
                                        2
                                    </div>
                                    <div class="w-[25%] h-full flex justify-center items-center selector" @click="SetSelector('size', 3)" :class="{'chose': $store.state.createlobbydata.size == 3}">
                                        3
                                    </div>
                                    <div class="w-[25%] h-full flex justify-center items-center selector" @click="SetSelector('size', 4)" :class="{'chose': $store.state.createlobbydata.size == 4}">
                                        4
