                                <div class="w-[50%] h-full  flex items-end justify-end">
                                    <div class="w-[25%] h-full flex justify-center items-center selector" @click="SetSelector('getvehicle', true)" :class="{'chose': $store.state.createlobbydata.getvehicle == true}">
                                        {{ $store.state.translate.yes }}
                                    </div>
                                    <div class="w-[25%] h-full flex justify-center items-center selector" @click="SetSelector('getvehicle', false)" :class="{'chose': $store.state.createlobbydata.getvehicle == false}">
                                        {{ $store.state.translate.no }}
