                            </div>

                        </div>
                    </div>
                    <div class="w-full h-[20%]  sell flex justify-center items-center" style="border-radius: 10px;background: #A49F6D;" @click="CreateLobby">{{$store.state.translate.createlobby}}</div>
                </div>
            </div>
            <div class="w-[10%] h-[5%] goback gap-3  flex justify-center items-center mt-5" @click="GoBack">
