  </template>
  
  <script>
  import $ from 'jquery';
  export default {
    name: 'Disclaimer',
    data() {
      return {
        clickedwikistate : 1,
        
    }
},
    props: {
      msg: String
    },
    
    methods : {
         SetClicked(id) {
              this.$store.state.checkbuttons[id] = !this.$store.state.checkbuttons[id]
         },
         GoBack() {
             if (this.$store.state.createlobbydata.hunttype.type == undefined) {
                this.$store.state.page = 'coop'
             } else {
                this.$store.state.createlobbydata.hunttype.type = undefined
             }
            
         },

         CreateLobby() {
            //console.log("hereee", this.$store.state.createlobbydata.hunttype.type, this.$store.state.createlobbydata.type, this.$store.state.createlobbydata.size)
            if (this.$store.state.createlobbydata.hunttype.type && this.$store.state.createlobbydata.type && this.$store.state.createlobbydata.size) {
                //console.log(JSON.stringify(this.$store.state.createlobbydata)) // js to lua create lobby
                $.post('http://m-hunting/createlobby', JSON.stringify({
				    data : this.$store.state.createlobbydata,
			    }));
                this.$store.state.createlobbydata = {type : undefined, size : undefined, getvehicle : undefined, contracts : -1, password : "", hunttype : {}, requiredpassword : false}
                this.$store.state.page = 'cooplobbies'
            }
         },
         SelectTypeofHunt() {
            this.$store.state.page = "birdspage"
         },
         TurnMainMenu() {
            this.$store.state.page = "main"
         },
         CalculateTotalPrice(count, itemname) {
            if (this.$store.state.itemprices[itemname]) {
                count = Number(count)
                return count * this.$store.state.itemprices[itemname]
            }
         },
         SetSelector(type, id) {
            if (type == 'type' && id == 'private') {
                this.$store.state.createlobbydata.requiredpassword = true
            } else if (type == 'type' && id == 'public') {
                this.$store.state.createlobbydata.requiredpassword = false
            }
            this.$store.state.createlobbydata[type] = id
         },
         GetImage2(image) {
            var imagte = require('@/assets/'+image+'')
            return {
                backgroundImage : 'url('+imagte+')'
            }
            
         },
         SetSelectedHuntType(id, type) {
            var plyerLevel = this.$store.state.PlayerData.level
            if (plyerLevel >= this.$store.state.categories[id][type].requiredLevel) {
                this.$store.state.createlobbydata.hunttype = {
                    type : id,
                    selectedtype : type
                }
            } 
         },

         SelectContract(id) {
            if (id == this.$store.state.createlobbydata.contracts) {
                return this.$store.state.createlobbydata.contracts = -1
            }
            this.$store.state.createlobbydata.contracts = id
         },
         GetAllList() {
            const foundData = []
            var arr = Object.keys(this.$store.state.categories);
            
            arr.forEach(key => {
              var element =  Object.keys(this.$store.state.categories[key]);
              
                element.forEach(key2 => {
                    if (key2 != "requiredLevel") {
                        var element2 = this.$store.state.categories[key][key2]
                        foundData.push(element2)
                    }

                });            
            });
            return foundData
         },
         CalculateBackgroundColor(type) {
            var color = "#98D6EA"
            if (type == "animals") {
                color = "#A49F6D"
            } else if (type == "predators" ) {
                color = "#A4776D"
            }
            return {
                background : color
            }
         },
         GetBackground(type) {
            if (type == "bird") {
                return {
                'bgbird': true,
                }
            } else if (type == "animal") {
                return {
                'bganimals': true,
                }
            } else if (type == "predator") {
                return {
                    'bgpredators': true,
                }
            } else {
                return {
                    'bgbird': true,
                }
            }

         },
         GetBackground(type) {
            if (type == "bird") {
                return {
                'bgbird': true,
                }
            } else if (type == "animal") {
                return {
                'bganimals': true,
                }
            } else if (type == "predator") {
                return {
                    'bgpredators': true,
                }
            } else {
                return {
                    'bgbird': true,
                }
            }

         },

         GetItemImage(itemname) {
            return require('../assets/'+itemname+'.png')
         },

         AddItemToBasket(itemdata) {
            if (this.$store.state.basket[itemdata.itemname]) {
                return
            }
            this.$store.state.basket[itemdata.itemname] = {
                count : itemdata.count,
                label : itemdata.label
            }
         },
         GetImage(image) {
            return require('@/assets/'+image+'')
         },
         SetPage(id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                this.$store.state.page = "gearbirds"
                this.$store.state.selecteddata.area = this.$store.state.categories[this.$store.state.selecteddata.type][id].name
            }
            
         },
         GetAccess(requiredlevel) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= requiredlevel) {
                return {
                    'opacityup': true,
                    text : this.$store.state.translate.select
                }
            }
            return {
                text : 'Lv.'+requiredlevel+' '+this.$store.state.translate.required
            }
         }
      },

    
  }
  </script>
  
