<template>
    <div  class="w-full h-full bg-[url(@/assets/main-bg.png)] bg-center bg-cover bg-no-repeat flex justify-center items-center flex-col gap-[2vh]">
        <div class="w-full h-[8vh]   flex justify-center   items-center  categorytitle">
            <p >{{$store.state.translate.coop}}</p>
            <div class="w-[10%] h-[8%]   absolute flex justify-center items-center gotomainmenu gap-3" @click="TurnMainMenu" style="margin-right: 75vw;">
                <i class="fas fa-chevron-left" style="font-size: 1.3vw;"></i>
                {{$store.state.translate.mainmenu}}
            </div>
        </div>
        <div class="w-full h-[60vh]  flex justify-center items-center gap-[2vh] ">
            <div class="w-[24%] h-[80%]  bg-[url(@/assets/joinlobby.png)] flex flex-col items-center bg-center bg-no-repeat bg-cover gap-[4vh] overflow-hidden opacity-98" style="border-radius: 12px;" >
                <div class="w-[90%] h-[20%] mt-2  flex justify-center items-center card-title"><p>{{$store.state.translate.joincoop}}</p></div>
                <div class="w-[80%] h-[30%]  flex justify-center  card-desc" style="margin-top: 5vh;"><p>{{$store.state.desc}}</p></div>
                <div class="w-[60%] h-[12.5%]  flex justify-center items-center card-button flex justify-center items-center" @click="SetCoopPage('cooplobbies')">{{$store.state.translate.select}}</div>
            </div>
            <div class="w-[24%] h-[80%]  bg-[url(@/assets/createlobby.png)] flex flex-col items-center bg-center bg-no-repeat bg-cover gap-[4vh] overflow-hidden opacity-98" style="border-radius: 12px;" >
                <div class="w-[90%] h-[20%] mt-2  flex justify-center items-center card-title"><p>{{$store.state.translate.createlobby}}</p></div>
                <div class="w-[80%] h-[30%]  flex justify-center  card-desc" style="margin-top: 5vh;"><p>{{$store.state.translate.createlobbydesc}}</p></div>
                <div class="w-[60%] h-[12.5%]  flex justify-center items-center card-button flex justify-center items-center" style="background: #A49F6D;" @click="SetCoopPage('createlobby')">{{$store.state.translate.select}}</div>
            </div>
        </div>
        <div class="w-[20%] h-[7%] gobck  flex justify-center items-center" @click="GoBack">{{$store.state.translate.goback}}</div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'Disclaimer',
    props: {
      msg: String
    },
    methods : {
         SetClicked(id) {
              this.$store.state.checkbuttons[id] = !this.$store.state.checkbuttons[id]
         },
         GoBack() {
            this.$store.state.page = "main"
         },
         SelectTypeofHunt() {
            this.$store.state.page = "predatorspage"
         },
         TurnMainMenu() {
            this.$store.state.page = "main"
         },
         SetPage(id) {
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                this.$store.state.page = "gearpredators"
                this.$store.state.selecteddata.area = this.$store.state.categories[this.$store.state.selecteddata.type][id].name
            }
            
         },
         SetCoopPage(id) {
            this.$store.state.page = id
         },
         GetAccess(id) {
            //console.log(this.$store.state.selecteddata.type)
            //console.log(JSON.stringify(this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel))
            const plyerlevel = this.$store.state.PlayerData.level
            if (plyerlevel >= this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel) {
                //console.log("ehehhe")
                return {
                    'opacityup': true,
                    text : this.$store.state.translate.select
                }
            }
            return {
                text : 'Lv.'+this.$store.state.categories[this.$store.state.selecteddata.type][id].requiredLevel+' '+this.$store.state.translate.required
            }
         }
      }
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    .categorytitle {
        font-family: 'Gilroy';
        font-size: 3vh;
        color: #FFFFFF;
    }
    .card-title{
        font-family: 'Gilroy';
        font-size: 2rem;
        line-height: 38px;
        text-align: center;
        color: #FFFFFF;
    }
    .card-desc {
        font-family: 'Gilroy-Semibold';
        font-size: 1.4vh;
        font-weight: 500;
        text-align: center;
        color: #FFFFFF;
        text-shadow: 0px 4px 17px rgba(0, 0, 0, 0.25);
    }
    .card-button{
        background: #A4776D; 
        border-radius: 9px;
        font-family: 'Gilroy-Semibold';
        font-size: 1.6vh;
        text-align: center;
        color: #FFFFFF;
    }
    .gobck {
        background: #A49F6D;
        box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        font-family: 'Gilroy';
        font-size: 1.8vh;
        color: #000000;
    }
    .gotomainmenu {
        color: #FFFDFD;
        font-size: 1.2rem;
        font-weight: 500;
        font-family: 'Gilroy-Semibold';
    }
    .opacityup {
        opacity: 95%;
    }
  </style>
  